import './App.css';
import proto from './grpc/safescale_grpc_web_pb'
import './grpc/safescale_pb'
//const { Tenant } = require('./grpc/safescale_grpc_web_pb');

//const { HelloRequest, RepeatHelloRequest } = require('./grpc/helloworld_pb');

const client = new proto.TenantServiceClient('http://' + window.location.hostname + ':50080', null, null);



function App() {

  function currentTenant() {
    client.Get({}, {}, (err, response) => {
      if (err) {
        console.log(err)
      } else {
        console.log(response.getMessage());
      }
    })
  }

  return (
    <>
      <button onClick={() => currentTenant()}>GetCurrentTenant</button>
    </>
  );
}

export default App;
